.creative {
    height: 500px;
    background: url('../../../public/images/about-bg.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed
}

.creative .creative-info {
    padding-top: 100px;
    width: 50%;
    float: right;
}

.creative .creative-info .info-title {
    font-weight: bold;
    font-size: 50px
}

.creative .creative-info .info-title span {
    font-weight: normal
}

.creative .creative-info .info-dir {
    font-size: 40px;
    color: #eb5424;
    margin-bottom: 20px
}

.creative .creative-info .info-desc {
    color: #000;
    margin-bottom: 0;
    line-height: 1.8
}

.creative .creative-info .info-desc a {
    text-decoration: none
}

@media (max-width:991px) {
    .creative {
        height: auto
    }

    .creative .creative-info {
        width: 100%;
        padding-top: 10px;
        float: none
    }

    .creative .creative-info .info-dir {
        font-size: 30px
    }
}